const initStateweb = {
  bank: {
    title: ` ${initStatepc.bank.title}${initStatepc.payment.title}`,
    link: '/',
    data: initStatepc.bank.data.slice(0, 4).concat(initStatepc.payment.data.slice(0, 2))
  },
  payment: {
    title: '',
    data: [{
      name: '', img: require('../assest/home/logo_yinlian.png'),
    }, {
      name: '', img: require('../assest/home/logo_lakala.png'),
    }]
  },
  developer: {
    title: '',
    link: '/',
    data: initStatepc.developer.data.slice(0, 6)
  }
}

const $doc = document.documentElement
const docWidth = $doc ? $doc.clientWidth : 0
const initStat = docWidth > 767 ? initStatepc : initStateweb
export function reducer (state = initStat, action ) {
  const $doc = document.documentElement
  const docWidth = $doc ? $doc.clientWidth : 0
  switch (action.type) {
    case WIDTHCHANGE:
      return  Object.assign({}, state, {
        screenWidth: docWidth
      })
    default:
      return { cooperate: state, screenWidth: docWidth }
  }
}

